import React from "react"

import { Layout } from "../components/Layout"
import { Page } from "../components/Page"

export default function DatasetDetails({ pageContext }) {
  const { dataset } = pageContext
  const imageUrl =
    (dataset.staticPreview && dataset.staticPreview.url) ||
    "http://placekitten.com/200/300"
  return (
    <Layout>
      <Page>
        <div>Name: {dataset.datasetLabel}</div>
        <img src={imageUrl} />
      </Page>
    </Layout>
  )
}
